.project-card {
  width: 70vh;
  border-radius: 13px;
  background-image: linear-gradient(212deg, var(--color-bg-variant), var(--color-bg));
  padding: 20px;
  margin: 10px 20px;
  height: min-content;
  font-size: 17px;
  box-shadow: 1px 4px 7px 3px #00000033;
}

.project-card:hover {
  box-shadow: 0 0 19px 0px #3a3f888c;
}

.list .project-card {
  width: 100vh;
  height: 100%;
}
.list .project-card-content {
  display: grid;
  grid-template-rows: 1fr 0.2fr;
  grid-template-columns: 2fr 2fr;
  gap: 5px 28px;
  grid-auto-flow: row;
  grid-template-areas:
    'image-wrapper info-wrapper info-wrapper'
    'image-wrapper btn-wrapper btn-wrapper';
  justify-items: start;
  transition: all 1s;
}

.info-wrapper {
  grid-area: info-wrapper;
}

.image-wrapper {
  min-height: 50%;
  grid-area: image-wrapper;
}

.list .image-wrapper {
  display: flex;
  align-items: center;
}

.project-image {
  width: 100%;
  border-radius: 9px;
}

.list .project-image {
  height: 100%;
  object-fit: cover;
}

.btn-wrapper {
  display: flex;
  margin-top: 10px;
  grid-area: btn-wrapper;
}

.list .btn-wrapper {
  margin-top: 0;
}

.project-description,
.tag {
  font-size: 16px;
}

.project-name {
  margin: 7px 0;
  font-size: 20px;
}

.tag {
  color: var(--color-bg);
  display: inline-block;
  padding: 3px 8px;
  margin: auto 4px 4px auto;
  background: var(--color-primary);
  border-radius: 6px;
  box-shadow: #00000040 4px 3px 20px, #ffffff40 1px 1px 0px inset;
}

.skills-wrapper {
  width: 100%;
  transition: max-height 1s;
  overflow: hidden;
  display: none;
}

.title-and-techs-wrapper {
  display: flex;
  justify-content: space-between;
}

.techs {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  position: relative;
}

.projects-section.grid .techs, .carrousel-mode .techs {
  justify-content: flex-start;
}
