.projects-container-home {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  position: relative;
}

.projects-container-home-wrapper {
  display: flex;
  max-width: 95vw;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
}

.projects-container-home-wrapper .project-card {
  width: min-content;
  cursor: pointer;
}

.projects-container-home-wrapper .project-description:hover {
  -webkit-transition: max-height 0.6s;
  -moz-transition: max-height 0.6s;
  -ms-transition: max-height 0.6s;
  -o-transition: max-height 0.6s;
  transition: max-height 0.6s;
}

.projects-container-home-wrapper .project-description {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  line-height: 20px;
  max-height: 60px;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.projects-container-home-wrapper .project-description:hover,
.projects-container-home-wrapper .description-wrapper:hover {
  overflow: visible;
  text-overflow: unset;
  display: block;
  max-height: 300px;
}

@media (max-width: 1000px) {
  .projects-container-home-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

.projects-container-home .switch-mode-btns {
  display: flex;
  position: absolute;
  top: 10px;
  right: 10%;
  padding: 10px;
  z-index: 3;
}

.projects-container-home .icon-wrapper {
  position: relative;
}